import { Component, OnInit, ViewChild } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { NgForm, NgModel, NgControl, FormsModule } from '@angular/forms';
import { LoopBackAuth, BooksApi, ContainerApi, CityApi } from '../../../../shared/loopback-sdk';
import { CookieService } from 'angular2-cookie/core';
import { Headers, Http, Response } from '@angular/http';
import { stringify } from 'querystring';
import { ShareService } from '../../../../providers/share-service/share-service';
declare var window: any;
declare var hostname: any;
declare var sdkPrefix;
@Component({
	templateUrl: 'edit-province.component.html'
})
export class EditProvinceComponent {
	@ViewChild('name') name: NgModel;
	@ViewChild('priority') priority: NgModel;
	public id: number;
	display: boolean = false; result: any;
	submitted: boolean = false;
	provinceName: any;
	busy: any;
	userDetails: any;
	rolename: any;
	loading: boolean = false;
	provinceStatus:boolean = false;
	bookImage:any;
	loginFlagToken:any;
	projectCount : number = 0;
	municipalityCount : number = 0;

	editProvince={
		"city_id":0,
		"city_name":"",
		"status": 0
	}
	constructor(private loopAuth: LoopBackAuth, private route: ActivatedRoute, private http: Http, private _cookieService: CookieService, private books: BooksApi, private router: Router, private containerApi: ContainerApi, private share: ShareService, private cityApi:CityApi) {
		this.userDetails = this.loopAuth.getCurrentUserData();
		this.rolename = this.userDetails.userDet.roles;
		this.id = this.route.snapshot.params['id'];
		this.loginFlagToken = this._cookieService.get(sdkPrefix+'id');
	}

	ngOnInit(): void {
		this.loading = true;
		var dataArrObj = {
			"city_id":this.id
		}
		this.cityApi.getProvince(dataArrObj).subscribe(data=>{
			
			if(data){
				let result = data.result[0];
				this.provinceName = result.city_name;
				this.provinceStatus = (result.status)?true:false;
				this.projectCount = result.usedCount;
				console.log("projectCount ",this.projectCount);
				this.editProvince={
					"city_id":this.id,
					"city_name": result.city_name,
					"status": result.status
				}

				this.cityApi.getMunicipalityCount({"city_id":this.id}).subscribe((data)=>{
					console.log("total :",data.result[0].total);
					let totalMunicipality = data.result[0].total;
					if(totalMunicipality > 0 ){
						this.municipalityCount = totalMunicipality;
					}
					this.loading = false;
				},(err)=>{
					this.loading = false;
					this.result = "Something went wrong";
					this.display = true;
					
				});


				this.checkCountProject(this.editProvince).then((total:number)=>{
					if(total > 0){
						this.loading = false;
						//this.result = "Municipality is already been used so it cannot be inactive";
						//this.display = true;
						this.projectCount = total;
					}else{
						this.checkCountContractor(this.editProvince).then((total:number)=>{
							if(total > 0){
								this.loading = false;
								//this.result = "Municipality is already been used so it cannot be inactive";
								//this.display = true;
								this.projectCount = total;
							}
						},(err)=>{
							this.loading = false;
							//this.result = "Something went wrong";
							//this.display = true;
						});
					}
				},(err)=>{
					this.loading = false;
					//this.result = "Something went wrong";
					//this.display = true;
				});

			}
		},
		err=>{
			console.log("err", err);
			this.loading = false;
		},
		()=>{

		});
	}

	goToListing() {
		// window.location.reload();
		this.router.navigate(["province"]);
	}

	async submit(provinceForm) {
		this.submitted = true;
		if (provinceForm.valid) {
			this.loading = true;
			
			this.editProvince.city_id = this.id;
			this.editProvince.city_name = this.capital_letter(this.provinceName);
			this.editProvince.status = (this.provinceStatus)?1:0;

			this.checkCountProject(this.editProvince).then((total)=>{
				if(total > 0 && !this.editProvince.status){
					this.loading = false;
					this.result = "Province is already been used so it cannot be inactive";
					this.display = true;
				}else{
					this.checkCountContractor(this.editProvince).then((total)=>{
						if(total > 0 && !this.editProvince.status){
							this.loading = false;
							this.result = "Province is already been used so it cannot be inactive";
							this.display = true;
						}else{
					
							this.cityApi.upsert(this.editProvince).subscribe((data)=>{
								this.loading = false;
								this.result = "success";
								this.display = true;

							},(err)=>{
								this.loading = false;
								this.result = "Something went wrong";
								this.display = true;
							});
						}
					},(err)=>{
						this.loading = false;
						this.result = "Something went wrong";
						this.display = true;
					});


				}
			},(err)=>{
				this.loading = false;
				this.result = "Something went wrong";
				this.display = true;
			});

			
		}
	}


	capital_letter(str) 
	{
		if(str != '' && str != null && str != undefined){
			str = str.split(" ");

			for (var i = 0, x = str.length; i < x; i++) {
				str[i] = str[i][0].toUpperCase() + str[i].substr(1);
			}
	
			return str.join(" ");
		}else{
			return str
		}
		
	}

	checkCountProject(dataObj){
		return new Promise((resolve,reject)=>{
			try{
				let access_token = this.loopAuth.getAccessTokenId();
				let city_id = dataObj.city_id;
				let cond = {"city_id": city_id};
				
				let api="/api/projects/count?where=";
				
				let url = hostname+api+encodeURI(JSON.stringify(cond))+"&access_token="+access_token;
				this.http.get(url).subscribe((data)=>{
					//console.log("Count ",data.json());
					resolve(data.json().count);
				},(err)=>{
					reject(err);
				});
			}
			catch(err){
				reject(true);
			}
		});
	}

	checkCountContractor(dataObj){
		return new Promise((resolve,reject)=>{
			try{
				let access_token = this.loopAuth.getAccessTokenId();
				let city_id = dataObj.city_id;
				let cond = {"city_id": city_id};
				
				let api="/api/contractor_profiles/count?where=";
				
				let url = hostname+api+encodeURI(JSON.stringify(cond))+"&access_token="+access_token;
				this.http.get(url).subscribe((data)=>{
					//console.log("Count ",data.json());
					resolve(data.json().count);
				},(err)=>{
					reject(err);
				});
			}
			catch(err){
				reject(true);
			}
		});
	}
	
}


